import Vue from 'vue'
import Router from 'vue-router'
import OrderSysLogin from './views/OrderSysLogin.vue'
import Category from './views/category/category.vue'
Vue.use(Router)

export default new Router({
  routes: [
	 { path: '/', component: OrderSysLogin },
	 
	 { path: '/category',component:Category},
	 { 
	 	path: '/userhandler', 
	 	name:'UserHandler',
	 	component: () => import(/* webpackChunkName: "about" */ './views/user/UserHandler.vue'),
	 	children: [
	 		{
	 		  path: '',
	 		  name:'UserAdd',
	 		  component: () => import(/* webpackChunkName: "about" */ './views/user/UserAdd.vue')
	 		},
	 		{
	 		  path: 'userchange',
	 		  name:'UserChange',
	 		  component: () => import(/* webpackChunkName: "about" */ './views/user/UserChange.vue')
	 		}
	 	]
	 },
	 {
	 	path:'/foodhandler',
	 	name:'FoodHandler',
	 	component:() => import(/* webpackChunkName: "about" */ './views/food/FoodHandler.vue'),
	 	children:[
	 		{
	 			path:'foodadd',
	 			name:'FoodAdd',
	 			component:() => import(/* webpackChunkName: "about" */ './views/food/FoodAdd.vue')
	 		},
	 		{
	 			path:'foodchange',
	 			name:'FoodChange',
	 			component:() => import(/* webpackChunkName: "about" */ './views/food/FoodChange.vue')
	 		}
	 	]	
	 },
	 {path:'/order',name:'Order',component:() => import('./views/order/order.vue')},
	 {path:'/orderTable',name:'OrderTable',component:() => import('./views/order/orderTable.vue')},
	 {path:'/orderAdd',name:'OrderAdd',component:() => import('./views/order/orderAdd.vue')},
	 {path:'/cooking',name:'Cooking',component:()=>import('./views/cooking/cooking.vue')},
  ]
})
