<template>
	<div>
	 <el-table :data="tableData" style="width: 80%">
	    <el-table-column label="日期" width="180">
	      <template slot-scope="scope">
	        <i class="el-icon-time"></i>
	        <span style="margin-left: 10px">{{handleDate(""+scope.row.createTime)}}</span>
	      </template>
	    </el-table-column>
	    <el-table-column label="姓名" width="180">
	      <template slot-scope="scope">
	        <el-popover trigger="hover" placement="top">
	          <p>姓名: {{ scope.row.name }}</p>
	          <p>住址: {{ scope.row.createBy }}</p>
	          <div slot="reference" class="name-wrapper">
	            <el-tag size="medium">{{ scope.row.name }}</el-tag>
	          </div>
	        </el-popover>
	      </template>
	    </el-table-column>
		<el-table-column label="状态">
			<template slot-scope="scope">
				<el-tag size="medium" v-if="scope.row.status==1">正常</el-tag>
				<el-tag size="medium" v-if="scope.row.status==2">禁用</el-tag>
			</template>
		</el-table-column>
	    <el-table-column label="操作">
	      <template slot-scope="scope">
	        <el-button
				size="mini" style="margin-left: 10px;"
				@click="handleCategoryState(scope.$index, scope.row,2)">禁用</el-button>
	        <el-button
			<el-button
				size="mini"
				@click="handleCategoryState(scope.$index, scope.row,1)">启用</el-button>
			<el-button
				style="margin-top: 10px;"
				size="mini"
				type="danger"
				@click="handleDelete(scope.$index, scope.row)">删除</el-button>
	      </template>
	    </el-table-column>
	  </el-table>
	  <el-button  @click="dialogVisible = true">添加</el-button>
	  
	  <el-dialog
	    title="提示"
	    :visible.sync="dialogVisible"
	    width="30%">
	    <el-input v-model="addCategoryName" placeholder="类别名字"></el-input>
	    <span slot="footer" class="dialog-footer">
	      <el-button @click="dialogVisible = false">取 消</el-button>
	      <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
	    </span>
	  </el-dialog>
	  
	  
	  </div>
</template>

<script>
	import {getAllCategory ,saveCategory,delCate,updateCategoryStatus} from '../../api/index.js'
	import category from '../../entity/category.js'
	import {dateUtil} from '../../util/dateUtil.js'
	export default{
		data(){
			return {
				addCategoryName:'',
				dialogVisible:false,
				tableData: [{
				"id":1,
				"name":"肉类",
				"type":1,
				"status":1,
				"sort":1,
				"createTime":"2019-10-11 00:49:51",
				"updateTime":"2019-10-11 00:49:54",
				"createBy":"1"}]
			}
		},
		methods: {
			handleCategoryState(index, row,state) {
			        console.log(index, row ,state);
					updateCategoryStatus({categoryId:row.id,state:state}).then(
						r=>{
							console.log(JSON.stringify(r.data))
							this.getAllCategorys()
						}
					)
			    },
			handleDelete(index, row) {
			        console.log(index, row);
					delCate({categoryId:row.id}).then(
						r=>{
							console.log(JSON.stringify(r.date))	
							this.getAllCategorys();
						}
					)
			    },
			handleDate(date){
				return date.substring(0,10);
			},
			getAllCategorys(){
				getAllCategory({}).then(r =>{
					console.log(JSON.stringify(r))
					console.log("状态吗："+r.data.code)
					if(r.data.code ==201){
						this.$router.push('/');
					}
					this.tableData = r.data;
				})
			}
			
		},
		created:function() {
			//请求类别数据
			this.getAllCategorys()
		},
	}
</script>

<style>
