<template>
  <div class="login">
    <h1> 源龙后台管理 </h1>
	<div class="conten">
		<el-input v-model="name" placeholder="名字"/>
		<br />
		<br />
		<el-input v-model="password" placeholder="密码"/>
		<br />
		<br />
		<el-button   @click="login()">登录</el-button>
		<br />
	</div>
    </div>
</template>

<script>
	import {login} from '../api/index.js'
export default {
  name: 'HelloWorld',
  data(){
	  return{
		  name:"",
		  password:"",
	  }
  },
  methods:{
	  login() {
			login({name:this.name,password:this.password}).then(r => {
			  // console.log("返回："+JSON.stringify(r))
			  if(r.status==200 && r.data!=''){
				  console.log("token:"+r.data);
				  this.$store.commit('set_token',r.date)
				  sessionStorage.token = r.data
				  this.$router.push({path:"/userhandler"})	
				  console.log("store_token:"+this.$store.state.token)
			  }
			})
	  }
  },
  props: {
    msg: String
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scss scoped>
h1{
	background: #42B983;
}
.conten{
	position: absolute;
	width: 30%;
	left: 40%;
}

.login{
	background: #2C3E50;
}
</style>
