import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
	token:''
  },
  mutations: {
	set_token:function(state,token){
		state.token = token
		sessionStorage.token = token
	},
	del_token(state){
		state.token =''
		sessionStorage.removeItem('token')
	}
  },
  actions: {
	set_token({commit},token){
		commit('set_token',token)
	}
  },
  
})
